'use strict';

var express = require('express');
var controller = require('./baseObject.controller');
import * as auth from '../../auth/auth.service';

var router = express.Router();

router.get('/:model', controller.index);
router.get('/:model/:id', controller.show);
router.post('/:model/', controller.create);
router.put('/:model/:id', controller.update);
router.patch('/:model/:id', controller.update);
router.delete('/:model/:id', controller.destroy);

module.exports = router;
